html{
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: #43c6ac; /* fallback for old browsers */
  background-image: -webkit-linear-gradient(to top, #43c6ac, #191654); /* Chrome 10-25, Safari 5.1-6 */
  background-image: -webkit-linear-gradient(bottom, #43c6ac, #191654);
  background-image: linear-gradient(to top, #43c6ac, #191654);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  
}

body {
  background: red;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  margin-top: 20%;
}


.App {
  text-align: center;
  height: 100%;
  margin: 0;
  background: transparent;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#tester {
  margin: 0 auto;
}

button {
  box-shadow: 3px 3px 5px #2f3d41;
}

.restaurant-card {
    box-shadow: 5px 5px 5px #2f3d41;
    background-color: #5c5858
}

